import React from 'react';
import { Asset } from '../common/asset';
import { connectEvaluation } from '../common/redux';

interface ICompSpeakerProps extends React.SVGProps<SVGSVGElement> {
  word: string;
}

export const CompSpeaker: React.FC<ICompSpeakerProps> = (props) => {
  const { word } = props;
  const [asset, setAsset] = React.useState<string | null>(null);
  const audio = React.useMemo(() => {
    const wordIndex = `${word}${'_'.repeat(word.length)}`;
    const audioEl = document.createElement('audio');
    const sourceEl = document.createElement('source');
    sourceEl.src =
      `https://www.oxfordlearnersdictionaries.com/media/american_english/us_pron/` +
      `/${wordIndex.slice(0, 1)}` +
      `/${wordIndex.slice(0, 3)}` +
      `/${wordIndex.slice(0, 5)}` +
      `/${word}__us_1.mp3`;
    sourceEl.type = 'audio/mpeg';
    audioEl.appendChild(sourceEl);
    return audioEl;
  }, [word]);

  React.useMemo(() => {
    (async () => setAsset(await Asset.fetch('speaker.png')))();
  }, []);

  return (
    <div
      style={{
        width: 180,
        textAlign: 'center',
        zIndex: 999,
      }}
    >
      <div
        style={{
          fontWeight: 'bold',
          margin: 16,
          cursor: 'pointer',
          height: 96,
          backgroundImage: `url(${asset})`,
          backgroundSize: 'contain',
          backgroundRepeat: 'no-repeat',
          backgroundPosition: 'center',
        }}
        onClick={() => audio.play()}
      />
    </div>
  );
};
