import { grpc } from '@improbable-eng/grpc-web';
import { dictionary } from '../proto/dictionary_pb_service';
import { Phonetic, DictRequest } from '../proto/dictionary_pb';
import { HOST } from '../common/defines';

export namespace Dictionary {
  export const lookup = async (word: string): Promise<any> => {
    const request = new DictRequest();
    request.setWord(word);

    const data: any = await new Promise((resolve) =>
      grpc.unary(dictionary.lookup, {
        request,
        host: HOST,
        onEnd: ({ message, status }: any) => {
          resolve(
            status
              ? null
              : {
                  word: message.getWord(),
                  phonetics: message.getPhoneticsList().map((phonetic: Phonetic) => ({
                    text: phonetic.getText(),
                    audio: phonetic.getAudio(),
                  })),
                }
          );
        },
      })
    );
    return data;
  };
}
