import React from 'react';
import { Asset } from '../common/asset';
import { Voice } from '../common/voice';

declare var MediaRecorder: any;
interface ICompRecorderProps extends React.SVGProps<SVGSVGElement> {
  word: string;
}

export const CompRecorder: React.FC<ICompRecorderProps> = (props) => {
  const [icons, setIcons] = React.useState<any>(null);
  const [recording, setRecording] = React.useState(false);
  const [predictedWord, predict] = React.useState(null);
  const { word } = props;
  const recorderEl = React.useMemo(() => {
    async function initialize() {
      const chunks: any[] = [];
      const stream: MediaStream = await navigator.mediaDevices.getUserMedia({ audio: true });
      const recorder = new MediaRecorder(
        stream
        // { mimeType: 'video/webm; codecs=vp9' }
      );
      recorder.ondataavailable = async (e: any) => {
        chunks.push(e.data);
        if (recorder.state === 'inactive') {
          try {
            const blob = new Blob(chunks, { type: 'audio/webm' });
            // const audioEl = document.createElement('audio');
            // audioEl.src = URL.createObjectURL(blob);
            // audioEl.play();

            const data = await new Response(blob).arrayBuffer();
            const [predictedWord] = await Voice.predict(new Uint8Array(data));
            predict(predictedWord);
          } catch (error) {
            console.log(error);
          }
        }
      };
      recorder.onstop = () => (chunks.length = 0);
      return recorder;
    }
    return initialize();
  }, [word]);

  React.useMemo(() => {
    (async () =>
      setIcons(await Promise.all([Asset.fetch('recording.png'), Asset.fetch('recorder.png')])))();
  }, []);
  const { transcript, confidence }: any = predictedWord || {};

  if (!icons) return null;
  return (
    <div
      style={{
        width: 180,
        textAlign: 'center',
        zIndex: 999,
      }}
    >
      <div
        className="button"
        style={{
          backgroundImage: `url(${icons[+!recording]})`,
        }}
        onClick={() => {
          predict(null);
          recorderEl.then((recorder) => {
            setRecording(recorder.start() || true);
            setTimeout(() => setRecording(recorder.stop() || false), 2000);
          });
        }}
      />
      {transcript && <div style={{ marginTop: -20 }}>{`${transcript}\n${confidence}`}</div>}
    </div>
  );
};
