import JSZip from 'jszip';
import { grpc } from '@improbable-eng/grpc-web';
import { voice } from '../proto/voice_pb_service';
import { Word, VoiceData } from '../proto/voice_pb';

export namespace Voice {
  const HOST = 'https://bacti.tk:8080';
  export const predict = async (buffer: Uint8Array): Promise<any> => {
    const request = new VoiceData();
    request.setBuffer(buffer);

    const data: Uint8Array = await new Promise((resolve) =>
      grpc.unary(voice.predict, {
        request,
        host: HOST,
        onEnd: ({ message, status }: any) =>
          resolve(
            status
              ? null
              : message.getWordsList().map((word: Word) => ({
                  transcript: word.getTranscript(),
                  confidence: word.getConfidence(),
                }))
          ),
      })
    );
    return data;
  };
}
